.nav-btn {
  background-color: white;
  border: 2px solid #007bff;
  color: #007bff;
  font-weight: 600;
  border-radius: 20px;
  padding: 6px 16px;
  transition: all 0.3s ease;
}

.nav-btn:hover {
  background-color: #007bff;
  color: white;
  transform: scale(1.05);
}

.nav-btn.active {
  background-color: #007bff;
  color: white;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.4);
}

.navbar-brand {
  font-size: 1.4rem;
}
