.techstack-section {
  overflow: hidden;
  width: 100%;
  background-color: #fff;
  padding-bottom: 30px;
}

.icon-carousel {
  position: relative;
  overflow: hidden;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px 0;
}

.icon-track {
  display: flex;
  align-items: center;
  animation: scroll-left 40s linear infinite; /* Adjust speed here */
}

.icon-box {
  flex: 0 0 auto;
  text-align: center;
  width: 150px; /* Adjust spacing */
  margin-right: 40px;
}

.icon-img {
  height: 70px;
  width: auto;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.icon-img:hover {
  transform: scale(1.1);
}

/* Smooth horizontal scroll animation */
@keyframes scroll-left {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}

/* Optional: fade edges for a soft look */
.icon-carousel::before,
.icon-carousel::after {
  content: "";
  position: absolute;
  top: 0;
  width: 80px;
  height: 100%;
  z-index: 2;
}

.icon-carousel::before {
  left: 0;
  background: linear-gradient(to right, white 60%, transparent);
}

.icon-carousel::after {
  right: 0;
  background: linear-gradient(to left, white 60%, transparent);
}

.benefits-list {
  list-style: none;
  padding-left: 0;
  max-width: 700px;
  margin: 0 auto 2rem auto;
  text-align: left;

  /* üîπ Added to center the list visually */
  position: relative;
  left: 90px;  /* adjust between 20‚Äì60px based on how centered you want it */
}

.benefits-list li {
  color: black;
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Second row style */
.second-row {
  margin-top: 15px; /* small gap between the two lines */
}

/* Reverse direction for the second line */
.reverse {
  animation: scroll-right 45s linear infinite; /* slower + reverse */
}

@keyframes scroll-right {
  from {
    transform: translateX(-50%);
  }
  to {
    transform: translateX(0);
  }
}

