/* Existing styles */

/* For the body and layout */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.gallery-container {
  display: flex;
  flex-direction: column;
  height: 80vh; /* defines full gallery height area */
  overflow: hidden; /* ensures only inner div scrolls */
  padding: 1rem;
}

.gallery-scroll {
  flex: 1;               /* take all remaining space */
  overflow-y: auto;       /* enables vertical scroll */
  padding-right: 8px;     /* space for scrollbar */
  scrollbar-width: thin;  /* Firefox */
}

.gallery-scroll::-webkit-scrollbar {
  width: 8px;
}

.gallery-scroll::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}



.hero {
  background: linear-gradient(90deg, #e8f0ff, #ffffff);
  border-radius: 8px;
  padding: 2rem;
}

.gallery-img {
  width: 100%;
  height: 250px;          /* uniform height for all images */
  object-fit: cover;      /* crop edges if needed but keeps image proportion */
  border-radius: 8px 8px 0 0; /* matches Bootstrap card radius */
  transition: transform 0.3s ease;
}

/* Small hover zoom effect for professional look */
.gallery-img:hover {
  transform: scale(1.03);
}

/* For mobile screens */
@media (max-width: 768px) {
  .gallery-img {
    height: 180px;
  }
}

.icon-container-wrapper {
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 120px;
  display: flex;
  align-items: center;
}

.icon-row {
  display: flex;
  align-items: center;
  gap: 2rem;
  /* The width should be at least twice the icon set for seamless loop */
  width: max-content;
  animation: marquee 20s linear infinite;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.icon-container {
  flex-shrink: 0;
  text-align: center;
}

.icon-container img {
  max-height: 80px;
  object-fit: contain;
}

